export default class PageModel {
  constructor(key) {
    this.key = key;
    this.videos = [];
    this.nextPageToken = '';
  }

  getVideoArray(searchStr, fn, videoPerPage, currentPage) {
    let pageToken = '';
    if ((this.videos.length - videoPerPage * currentPage) < 5) {
      pageToken = this.nextPageToken;
    }
    if (searchStr !== '' || pageToken !== '') {
      const url = `https://www.googleapis.com/youtube/v3/search?pageToken=${pageToken}&key=${this.key}&part=snippet&maxResults=15&q=${searchStr}`;
      fetch(url)
        .then(res => res.json())
        .then((res) => {
          console.log(res);
          if (pageToken !== '') {
            this.videos = this.videos.concat(res.items);
            this.nextPageToken = res.nextPageToken;
            console.log(this.videos);
          } else {
            this.videos = res.items;
            this.nextPageToken = res.nextPageToken;
          }
          // eslint-disable-next-line max-len
          const resp = this.videos.slice((currentPage - 1) * videoPerPage, currentPage * videoPerPage);
          console.log(resp);
          fn(resp);
        })
        .catch();
    }
    const resp = this.videos.slice((currentPage - 1) * videoPerPage, currentPage * videoPerPage);
    fn(resp);
  }

  getRate(id) {
    fetch(`https://www.googleapis.com/youtube/v3/videos?id=${id}&key=${this.key}&part=statistics`)
      .then(res => res.json())
      .then((res) => {
        console.log(res);
        return res;
      })
      .catch();
  }
}
